

/** ============  ============ */
export interface TransactionError extends Error {
    failureType: FailureType;
}

/** ============  ============ */
export enum FailureType {
    CONNECTION_FAILURE = "",
    PRECONDITION_FAILURE = "",
    POSTCONDITION_FAILURE = "",
    UNDEFINED_FAILURE = ""
}

/** ============  ============ */
export interface TransactionResponse<T> {
    isFinished: boolean;
    error?: TransactionError;
    data: T;
}
